nav .nav-link {
  position: relative;
  color: white;
  text-decoration: none;
  font-size: 1.2rem;
  font-weight: 400;
  transition: color 0.3s ease, font-size 0.3s ease, font-weight 0.3s ease;
}

nav .nav-link:hover {
  color: #89d1d0!important;
  font-size: 1.4rem;
  font-weight: 700;
}
.nav-link-custom {
  margin-left: 24px;
  margin-right: 24px;
}

.nav-link:hover ~.nav-link, .nav-link:has(~.nav-link:hover) {
  color: grey;
}
